import React, { Component } from 'react';
import SearchBar from './videos-youtube/containers/search-bar';
import Video from './videos-youtube/components/video';
import VideoEmbed from './videos-youtube/components/video-embed';
import YTSearch from 'youtube-api-search';
import { API_KEY } from './utils/apiKey';
import '../news/news.css'
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      videos: [],
      selectedVideo: null,
      status: true
    };
    this.videoSearch('Corona Viet Nam');

  }


  async videoSearch(term) {

    await YTSearch({ key: API_KEY, term }, videos => {
      this.setState({
        videos,
        selectedVideo: videos[0]
      });
      setTimeout(
        this.ytseach2(term), 0);

    });




  }
  ytseach2 = (term) => {
    var term = term + 'và Mỹ';
    var videos1 = this.state.videos;

    let promise = new Promise((resolve, reject) => {
      YTSearch({ key: API_KEY, term }, videos => {

        // videos.forEach(vd => {
        //   videos1.push(vd);      
        // });
        resolve(videos)
      });
    })

    promise.then((videos) => {
      this.setState({
        videos: [...this.state.videos, ...videos]
      });
    })
  }

  onChangeUI = () => {
    this.setState({
      status: !this.state.status
    });
  };

  render() {

    var enlementRender=this.state.status===true?<div >
    <SearchBar onSearch={term => this.videoSearch(term)} />

    <VideoEmbed video={this.state.selectedVideo} />
    <Video
      videos={this.state.videos}
      onVideoSelect={selectedVideo => this.setState({ selectedVideo })}
    />
  </div>:<div>
  <SearchBar onSearch={term => this.videoSearch(term)} />
        {this.state.videos.map(video => {
          return (
            <VideoEmbed video={video} />
          );
        })}
 
  </div>;
    return (
      <div>
        <div className="row">
          <div className="tm-section-header section-margin-top">
            <div className="col-lg-4 col-md-3 col-sm-3"><hr /></div>
            <div className="col-lg-4 col-md-6 col-sm-6"><h2 className="tm-section-title">Videos</h2>  <button className="block12" onClick={this.onChangeUI}>Thay đổi giao diện</button></div>
            <div className="col-lg-4 col-md-3 col-sm-3"><hr /></div>
          </div>
        </div>
        {enlementRender}
        
      </div>
    );
  }

}
export default App;
